body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #e2dcff;
  min-height: 100vh; }

#root {
  padding-top: 10vh; }

html {
  font-size: 18px; }

h1,
h2 {
  font-family: adelle, serif;
  line-height: 1.1;
  margin-bottom: 1rem; }

h1 {
  font-size: 2.618rem;
  color: #25262b; }

h2 {
  font-size: 1.618rem;
  color: #25262b;
  font-weight: 400; }

.grid {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center; }

input {
  height: 3rem;
  border-radius: 8px;
  width: 30vw;
  background: #6046ff;
  box-shadow: 0 2rem 4rem 0 #8677ff;
  font-size: 1.618rem;
  color: #ffffff;
  text-align: center;
  outline: none;
  border: none; }

